<template>
    <section class="m-importcar-desc">
        <v-header :type = 2>平行进口车</v-header>
        <scroll class="m-desc-content" ref="mDesc">
            <v-slider v-if="carImgArr.length">
                <div class="slider-item" v-for="(item,index) in carImgArr" :key="index">
                    <img :src="item" alt="">
                </div>
            </v-slider>
            <div class="weui-cells" v-if="Object.keys(importCar).length != 0">
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <h3>{{`${importCar.carName}${importCar.carSeriesName}`}}</h3>
                        <div class="price clearfix">
                            <div class="recommond-price">
                                <p>参考价：<span>{{importCar.referenPrice}}</span></p>
                            </div>
                            <div class="market-price">
                                <p>市场价：<del>{{importCar.marketPrice}}</del></p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <p class="save-money">最高节省：<span>{{importCar.maxSavePrice}}</span></p>
                        <p class="discount">凭单优惠（预购单）至少{{importCar.favourMoney}}</p>
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <div class="car-brand">
                            车辆的种类：<span>{{importCar.classType}}</span>
                        </div>
                        <div class="car-level">
                            车辆级别：<span>{{importCar.cartType}}</span>
                        </div>
                        <div class="car-color">
                            车身颜色：<span v-for="(item,index) in getCarColor(importCar.colors)" :key="index" :style="{background:item}"></span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="weui-cells">
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <v-panel :title="baseConfig">
                            <ul class="m-config-list">
                                <li class="m-config-item">
                                    <p class="m-config-name">车身机构</p>
                                    <p class="m-config">{{importCar.cheshenjiegou}}</p>
                                </li>
                                <li class="m-config-item">
                                    <p class="m-config-name">发动机</p>
                                    <p class="m-config">{{importCar.fadongjixinghao}}</p>
                                </li>
                                <li class="m-config-item">
                                    <p class="m-config-name">驱动形式</p>
                                    <p class="m-config">{{importCar.qudongtype}}</p>
                                </li>
                                <li class="m-config-item">
                                    <p class="m-config-name">综合耗油</p>
                                    <p class="m-config">{{importCar.cheshenjiegou}}</p>
                                </li>
                                <li class="m-config-item">
                                    <p class="m-config-name">长/宽/高</p>
                                    <p class="m-config">{{`${importCar.length}/${importCar.width}/${importCar.height}mm`}}</p>
                                </li>
                                <li class="m-config-item">
                                    <p class="m-config-name">变速箱</p>
                                    <p class="m-config">{{importCar.biansuxiangtype}}</p>
                                </li>
                                <li class="m-config-item">
                                    <p class="m-config-name">燃料形式</p>
                                    <p class="m-config">{{importCar.ranliaotype}}</p>
                                </li>
                            </ul>
                        </v-panel>
                    </div>
                </div>
                 <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <button class="weui-btn">全部配置</button>
                    </div>
                </div>
            </div>
            <div class="weui-cells">
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                         <v-panel :title="questions">
                             <img :src="importCar.oftenQuestionUrl" alt="" width="100%">
                        </v-panel>
                    </div>
                </div>
            </div>
            <div class="weui-cells">
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                         <v-panel :title="carDesc"> 
                             <div v-html="importCar.descUrl"></div>                         
                        </v-panel>
                    </div>
                </div>
            </div>
        </scroll>
         <div class="weui-tabbar">
            <a class="weui-tabbar__item">
                <i class="weui-tabbar__icon icon-service"></i>
                <p class="weui-tabbar__label">联系客服</p>
            </a>
            <a class="weui-tabbar__item search-price">
                <p class="weui-tabbar__label">底价查询</p>
            </a>
        </div>
        <router-view></router-view>
    </section>
</template>

<script>
import vSlider from '@/components/mobile/m-slider'
import vHeader from '@/components/mobile/m-header'
import vPanel from '@/components/mobile/m-panel'
import Scroll from '@/components/base/scroll/scroll'
import {MOBILE_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data(){
        return{
            importCar:{},
            carImgArr:[],
            baseConfig:"基本配置",
            questions:"常见问题",
            carDesc:"车辆信息"
        }
    },
    props:{
        carId:{
            type:Number,
            default:2
        }
    },
    components:{
        vSlider,
        vHeader,
        Scroll,
        vPanel
    },
    methods:{
        getcarDesc(){
            let _this = this;
            let data = new Object();
            data.cartId = this.carId;
            data.platForm = '020005';
            $.ajax({
                url:MOBILE_COMMONURL+"/queryImportCarDetail",
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    console.log(data);
                    if(data.code == SUCCESS_CODE){
                        _this.carImgArr = data.data.carDetail.icons;
                        _this.importCar = data.data.carDetail;
                    }
                }
            })
        },
        getCarColor(colors){
            return colors.split(",")
        }
    },
    mounted(){
        this.getcarDesc();
        this.$refs.mDesc.$el.style.top= "44px"
    }
}
</script>



